export default {
  name: 'information',
  data(){
    return {
      content: ''
    }
  },
  methods: {
    getIndexData(){
      let id = this.$route.params.id
      this.http.get(this.ports.home.index+'/'+id, res =>{
        this.isLoading = false
        this.$store.commit('changeLoading',false)
        console.log(res)
        if(res.success){
          this.content = res.data
        }else{
        }
      })
    }
  },
  mounted(){
    this.getIndexData()
  }
}



